
'use client';

import Image from 'next/image';
import Link from 'next/link';
import type { Building, User, Competition, CompetitionStatus } from '@/types'; // Assuming Competition type is available
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { ShieldAlert, CalendarDays, Users, Ticket, ArrowRight } from 'lucide-react';
import { cn } from '@/lib/utils';
import { format } from 'date-fns';

interface BuildingCardProps {
  building: Building;
  currentUser: User | null;
  competition?: Competition; // Optional: pass competition details if available
}

const getStatusBadgeClass = (status?: CompetitionStatus | BuildingStatus) => {
  if (!status) return 'bg-muted text-muted-foreground';
  switch (status) {
    case '进行中':
    case '有效':
      return 'bg-green-500/20 text-green-400 border-green-500/30';
    case '策划中':
    case '等待审核':
      return 'bg-yellow-500/20 text-yellow-400 border-yellow-500/30';
    case '评审中':
      return 'bg-purple-500/20 text-purple-400 border-purple-500/30';
    case '已结束':
    case '已归档':
      return 'bg-gray-500/20 text-gray-400 border-gray-500/30';
    case '违规':
    case '作弊':
    case '无效':
      return 'bg-red-500/20 text-red-400 border-red-500/30';
    default:
      return 'bg-muted text-muted-foreground';
  }
};


export default function BuildingCard({ building, currentUser, competition }: BuildingCardProps) {
  // Use building.status for works, competition.status for activities/events
  const displayStatus = competition ? competition.status : building.status;
  const isInteractive = building.status === '有效' && (!competition || competition.status === '进行中' || competition.status === '已结束' || competition.status === '评审中');
  const CardWrapper = isInteractive ? Link : 'div';
  const linkHref = competition ? `/competition/${competition.id}` : (isInteractive ? `/building/${building.id}` : '#');

  const cardTitle = competition ? competition.name : building.name;
  const cardDescription = competition ? competition.description : building.description;
  // Date formatting: use competition start date or building submission date
  let dateToDisplay = competition ? competition.startDate : building.submissionDate;
  let formattedDate = '日期未知';
  if (dateToDisplay) {
    try {
      formattedDate = format(new Date(dateToDisplay), 'yyyy年MM月dd日');
    } catch (e) { console.error("Date formatting error", e); }
  }
  
  const imageUrl = building.imageUrl || (competition ? `https://placehold.co/600x338.png?text=${encodeURIComponent(competition.name)}` : 'https://placehold.co/600x338.png');
  const aiHint = building.aiHint || (competition ? `event ${competition.name}` : "minecraft architecture");


  return (
    <Card 
      className={cn(
        "bg-card border-border overflow-hidden shadow-lg hover:shadow-primary/20 transition-all duration-300 flex flex-col h-full group relative",
        !isInteractive && "cursor-not-allowed opacity-80"
      )}
    >
      <CardWrapper
        href={linkHref}
        onClick={!isInteractive ? (e) => e.preventDefault() : undefined}
        className="flex flex-col flex-grow"
        aria-disabled={!isInteractive}
        tabIndex={!isInteractive ? -1 : undefined}
      >
        <div className="relative aspect-[16/9] w-full overflow-hidden">
          <Image
            src={imageUrl.replace('800x600', '600x338').replace('600x400', '600x338')}
            alt={cardTitle}
            layout="fill"
            objectFit="cover"
            data-ai-hint={aiHint}
            className={cn(
              "transform transition-transform duration-300",
              isInteractive && "group-hover:scale-105"
            )}
          />
          {displayStatus && (
             <Badge className={cn(
                "absolute top-3 right-3 px-2 py-1 text-xs font-semibold rounded",
                getStatusBadgeClass(displayStatus)
              )}>
              {displayStatus}
            </Badge>
          )}
          {!isInteractive && building.status !== '有效' && (
            <div className="absolute inset-0 bg-destructive/30 backdrop-blur-sm flex flex-col items-center justify-center text-center p-4 z-10">
              <ShieldAlert className="h-10 w-10 text-destructive-foreground mb-2" />
              <p className="text-md font-semibold text-destructive-foreground">状态: {building.status}</p>
              <p className="text-xs text-destructive-foreground/80">此作品当前不可交互。</p>
            </div>
          )}
        </div>
        <CardHeader className="pt-4 pb-2">
          <CardTitle className={cn(
            "text-lg font-semibold text-card-foreground leading-tight", // Adjusted size for screenshot
            isInteractive && "group-hover:text-primary transition-colors"
          )}>
            {cardTitle}
          </CardTitle>
        </CardHeader>
        <CardContent className="flex-grow pt-1 pb-3 space-y-2">
          <div className="flex items-center text-xs text-muted-foreground gap-1">
            <CalendarDays size={14} /> 
            <span>{formattedDate}</span>
          </div>
          <p className="text-sm text-muted-foreground leading-relaxed line-clamp-2">{cardDescription}</p>
        </CardContent>
      </CardWrapper>
      <CardFooter className="flex justify-between items-center mt-auto pt-3 pb-4 px-4 border-t border-border/50">
        <div className="flex items-center gap-3 text-xs text-muted-foreground">
          {/* Placeholder for participants and code, as in screenshot */}
          <div className="flex items-center gap-1">
            <Users size={14} />
            <span>{competition ? (competition.id === 'comp1' ? 120 : (competition.id === 'comp2' ? 95 : 50)) : building.votes} 人参与</span>
          </div>
          <div className="flex items-center gap-1">
            <Ticket size={14} />
            <span>{competition ? competition.id.toUpperCase() : building.id.substring(0,6).toUpperCase()}</span>
          </div>
        </div>
        <Button 
          asChild 
          size="sm" 
          variant="ghost" 
          className="text-primary hover:bg-primary/10 hover:text-primary px-2 py-1 h-auto text-sm"
          disabled={!isInteractive}
        >
          <Link href={linkHref}>
            查看{competition ? '活动' : '作品'}
            <ArrowRight size={16} className="ml-1" />
          </Link>
        </Button>
      </CardFooter>
    </Card>
  );
}
